    getTime() {
      var myDate = new Date()
      var myYear = myDate.getFullYear()
      var myMonth = myDate.getMonth() + 1
      var myToday = myDate.getDate()
      // var myDay = myDate.getDay()
      var myHour = myDate.getHours()
      var myMinute = myDate.getMinutes()
      var myseconds = myDate.getSeconds()

      this.nowTime =
        `<span class="year">${myYear}-${this.fillZero(myMonth)}-${this.fillZero(myToday)} </span>` +
        `<span class="time" style="font-size:0.35rem!important">${this.fillZero(myHour)}:${this.fillZero(myMinute)}:${this.fillZero(myseconds)} </span>`
    },

    fillZero(str) {
      var realNum
      if (str < 10) {
        realNum = '0' + str
      } else {
        realNum = str
      }
      return realNum
    },

    init() {
      this.getPieOption('pie1', this.pie1Data)
      this.getPieOption('pie2', this.pie2Data)
      this.getLineOption('line')
      this.getTableOption('lineRef')
      this.getWater('waterRef', '#25b2ae')
      this.getWater('waterRef2', '#2095b0')
      this.getWater('waterRef3', '#c2a95e')
      this.getdoubleLine('doubleLineRef')
