    },
    getChartTable(elRef, options) {
      this.$nextTick(() => {
        if (this.$refs[elRef]) {
          const myChart = this.$echarts.init(this.$refs[elRef] || document.getElementById(elRef))
          myChart.clear()
          myChart.setOption(options)

          window.addEventListener('resize', () => {
            myChart.resize()

            this.chartssize(this.refs.chartFather, myChart)
          })
        }
      })
    },

    chartssize(container, charts) {
      function getStyle(el) {
        if (window.getComputedStyle) {
          return window.getComputedStyle(el, null)
        } else {
          return el.currentStyle
        }
      }
      let hi = getStyle(container, 'height').height
      charts.style.height = hi
    },
  },
  watch: {},
  computed: {},
  filters: {},
